.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: $basic-white;
  text-transform: uppercase;
  background-color: $basic-green;

  &:hover {
    background-color: $basic-green-hover;
  }

  &:active {
    color: rgba($color: $basic-white, $alpha: 0.3);
  }
}
